/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

html,
button,
input,
select,
textarea {
    color: #222;
}

::selection {
    background: #b3d4fc;
    text-shadow: none;
}

img {
    vertical-align: middle;
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

textarea {
    resize: vertical;
}

.chromeframe {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}


html {
    font-size: 10px;
    font-family: 'Muli', sans-serif;
}

section, header {
    font-size: 20px;
}
header {
  background: #f9dfea;
}

.header {
  max-width: 80rem;
  padding: 0.5rem 0;
  margin: auto;
  position: relative;
  display: flex;
}

@media (max-width: 800px) {
  .header {       
    margin: 0 1rem;
  }
}

.logo {
  background: url("/logo.png") no-repeat;
  background-position: left center;
  text-indent: -9999px;
  height: 5rem;
  width: 12rem;
  margin-top: 1rem;
  background-size: contain;
}

.links {
  line-height: 7rem;
}

.links.links--left {
  margin-left: 15px;
}

.links.links--right {
  flex: 100 1 auto;
  text-align: right;
}

.links a.icon {
  font-size: 1.2em;
  padding: 0 5px;
}

.links a {
  color: #33232a;
  font-size: 17px;
  padding: 0 8px;
  text-decoration: none;
}

.links a:not(.icon):hover {
  text-decoration: underline;
}

@media (max-width: 400px) {
  .header {       
    flex-direction: column;
  }
  .links {
    line-height: 5rem;
  }
  .logo {
    width: 100%;
    background-position: center center;
  }
  .links.links--left, .links.links--right {
    text-align: center;
  }
}

section {
  max-width: 80rem;
  padding: 4rem 0;
  margin: auto;
  line-height: 1.2em;
}

section h1 {
  color: #33232a;
  margin-bottom: 4rem;
  font-size: 25px;
}

@media (max-width: 840px) {
  section h1 {
    padding: 0 4rem;
    text-align: center;
  }
}

.blocks {
  display: flex;
  flex-direction: row;
  sbackground: green;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-right: -4rem;
}

@media (max-width: 840px) {
  .blocks {
    justify-content: center;
  }
}

.block {
  display: block;
  position: relative;
  width: calc((80rem - 8rem) / 3);
  height: calc((80rem - 8rem) / 3);
  box-sizing: border-box;
  color: #33232a;
  margin-bottom: 4rem;
  margin-right: 4rem;
}

.block--em {
  padding: 2rem;
  background-color: #f9dfea;
  color: #33232a;
}

a.block--ig {
  background-size: cover;
  text-decoration: none;
  position: relative;
  border-radius: 2px;
  transition: transform 0.3s;
  font-size: 0;
}

a.block.block--ig:hover {
  transform: scale(1.02);
}

.blocks--ig-more {
  width: 100%;
  margin-right: 0;
}

.blocks--ig-more a {
  text-decoration: none;
  color: #33232a;
  text-align: center;
  margin-top: 2rem;
  width: 100%;
}

.block--wide {
  width: calc((80rem - 2rem) / 3 * 2);
}

@media (max-width: 840px) {
  .block.block--wide {
    width: calc(100vw - 4rem);
    padding: 0 4rem;
    height: auto;
  }
}

.block--loading {
  display: flex;
  align-content: center;
}

.block--loading .loader {
  margin-top: auto;
}

@media (min-width: 840px) {
  .block__scroll-content {
    overflow-y: scroll;
    height: 100%;
    margin-bottom: 3rem;
  }

  .block__scroll-content *:last-child {
    margin-bottom: 3rem;
  }

  .block__scroll-content:after {
    content: "";
    position: absolute;
    background: linear-gradient(0, #fff, transparent);
    width: 100%;
    bottom: 0;
    height: 3rem;
    width: 100%;
  }
}

.block--text {
  font-size: 1.7rem;
  line-height: 2rem;
}

p {
  margin-bottom: 1.5rem;
}

strong {
  font-weight: bold;
}

.block h1 {
  margin-bottom: 0.5rem;
  font-weight: bold;
  font-size: 22px;
}

.block a, .block a:active, .block a:hover, .block a:visited {
  color: #33232a;
}

.error {
  text-align: center;
  line-height: 1.5em;
  color: #ccc;
}

.content.content--about-me {
  display: flex;
}

.content.content--about-me .picture {
  background: url("/kat.jpg") no-repeat;
  background-position: left top;
  background-size: contain;
  flex: 1 0 auto;
  margin-left: 1rem;
  
  width: 20%;
  min-width: 26rem;
  min-height: 20rem;
}

.content.content--about-me .text-content {
  display: block;
  margin: 0 3rem;
}

@media (max-width: 600px) {
  .content.content--about-me {
    flex-direction: column;
    padding-top: 2rem;
  }

  .content.content--about-me .picture {
    background-position: center top;
    width: 100%;
    margin-bottom: 2rem;
  }
}

.loader {
  width: 80px;
  height: 80px;
  margin: auto;
  margin-top: 3rem;
  position: relative;
}
.loader div {
  position: absolute;
  border: 4px solid #f3c9db;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: loader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: loader 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.loader div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
@-webkit-keyframes loader {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}
@keyframes loader {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}

.block--signup-form h2 {
  font-size: 17px;
}

.block--signup-form label {
  display: block;
  margin-top: 10px;
  font-size: 1rem;
}

.block--signup-form input {
  width: 100%;
  border: none;
  box-sizing: border-box; 
  font-size: 1.5em;

  padding: 5px;
}

.block--signup-form input[type="submit"] {
  font-size: 1.5rem;
  margin-top: 15px;
  border: 2px solid white;
  background: #f9dfea;
}
